@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@500&family=Julius+Sans+One&family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@500&family=Julius+Sans+One&family=Raleway:wght@300&display=swap);
.about {
  height: 100%;
  width: 100%;
  background-color: rgba(249, 248, 246, 255);
  text-align: center;
}

.name-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.profile-pic {
  border-radius: 50%;
  width: 300px;
  height: 300px;
  padding: 40px;
}

.about-contents {
  padding-top: 30px;
  display: flex;
  justify-content: center;
}

.intro-para {
  height: 600px;
  width: 400px;
  font-family: "Raleway", sans-serif;
  line-height: 140%;
  color: #989da6;
  /* color: #496772; */
  margin-left: 100px;
  white-space: pre-wrap;
  padding-top: 10px;
  font-size: 1.25vw;
}

.my-name {
  margin: 150px auto 0 auto;
  font-family: "Julius Sans One", sans-serif;
  font-size: 300%;
  color: #d4e8dc;
  width: 30%;
}

.header-list {
  overflow: hidden;
  position: fixed;
  width: 100%;
  /* background-color: #989da6; grey */
  background-color: #496772;
  top: 0;
  height: 50px;
  margin: 0;
}

.list-wrapper {
  display: flex;
  list-style: none;
  align-items: stretch;
  justify-content: space-between;
}

.header-item {
  display: block;
  flex: 0 1 auto;
  list-style-type: none;
  font-family: "Raleway", sans-serif;
  color: #d4e8dc;

  padding-right: 10px;
  cursor: pointer;
}

.section-title-container {
  display: flex;
  justify-content: center;
}

.section-title {
  padding-top: 100px;
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
}

.subheading-container {
  display: flex;
  justify-content: center;
  margin-top: 0;
}

.subheading-text {
  font-family: "Raleway", sans-serif;
  color: #989da6;
  font-size: 1.25em;
  margin-top: 10px;
}

.skill {
  max-width: 600px;
  margin: 2rem auto;
  right: 380px;
}

.skill-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  background-color: #d4e8dc;
}

.skill-title:hover {
  background-color: #d4e8dc;
}

.skill-title,
.skill-content {
  padding: 1rem;
}

.skill-content {
  background-color: #d4e8dc;
  font-family: "Raleway", sans-serif;
  white-space: pre-wrap;
}

.skill-item {
  width: 500px;
}

.tech-skill {
  background-color: #d4e8dc;
  display: inline-block;
  margin: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  padding-left: 10px;
}

.tech-skill-text {
  font-size: 15px;
  margin-top: 0;
  margin-bottom: 0;
}

.skills {
  width: 100%;
  height: 916px;
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
}

.skills-title {
  padding-top: 100px;
  padding-bottom: 40px;
}

.skills-title-container {
  display: flex;
  justify-content: center;
}

.skills-container {
  display: flex;
  justify-content: space-around;
}

.tech-skills {
  display: flex;
  justify-content: space-evenly;
  width: 400px;
  height: 200px;
  flex-wrap: wrap;
}

.blog-post-box {
  border-style: solid;

  border-color: #d4e8dc;
  width: 220px;
  border-radius: 5%;
  background-color: #d4e8dc;
  text-align: center;
}

.blog-post-link {
  padding: 20px;
  font-family: "Raleway", sans-serif;
}

.blog-post-anchor,
.blog-post-anchor:visited,
.blog-post-anchor:active {
  text-decoration: none;
  color: #989da6;
}

.blog-post-anchor:hover {
  color: #496772;
}

.blog {
  width: 100%;
  height: 916px;
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
}

.blog-title {
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
  margin-bottom: 0;
  padding-top: 100px;
}

.blog-subheading {
  font-family: "Raleway", sans-serif;
  color: #989da6;
  font-size: 1.25em;
  margin-top: 10px;
}

.blog-title-container {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

.blog-subheading-container {
  display: flex;
  justify-content: center;
  margin-top: 0;
}

.blog-post-container {
  display: flex;
  justify-content: space-evenly;
  padding-top: 40px;
}

.hobby-container {
  width: 25%;
  text-align: center;
}

.hobby-image {
  height: 60%;
  width: 100%;
  /* border-radius: 5%; */
}

.hobby-description {
  font-family: "Raleway", sans-serif;
  line-height: 140%;
}

.hobbies {
  width: 100%;
  height: 916px;
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
}

.hobbies-title-container {
  display: flex;
  justify-content: center;
}

.hobbies-title {
  padding-top: 100px;
}

.hobbies-container {
  display: flex;
  justify-content: space-evenly;
  padding-top: 40px;
}

.project-container {
  /* border-style: solid; */
  width: 25%;
  text-align: center;
}

.project-image {
  height: 50%;
  width: 100%;
}

.project-description {
  font-family: "Raleway", sans-serif;
  line-height: 140%;
}

.project-anchor,
.project-anchor:visited {
  text-decoration: none;
  color: #989da6;
}

.projects {
  width: 100%;
  height: 916px;
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
}

.projects-title-container {
  display: flex;
  justify-content: center;
}

.projects-title {
  padding-top: 100px;
}

.projects-container {
  display: flex;
  justify-content: space-evenly;
  padding-top: 40px;
}

.past-experience {
  max-width: 600px;
  margin: 1rem;
}

.experience-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  background-color: #d4e8dc;
}

.experience-title:hover {
  background-color: #d4e8dc;
}

.experience-title,
.experience-content {
  padding: 1rem;
}

.experience-content {
  background-color: #d4e8dc;
  font-family: "Raleway", sans-serif;
  white-space: pre-wrap;
}

.experience-item {
  width: 500px;
}

.job-title {
  margin: 0;
}

.job-company {
  margin: 0;
  font-size: 0.75rem;
}

.experience {
  width: 100%;
  height: 916px;
  font-family: "Julius Sans One", sans-serif;
  color: #989da6;
}

.experience-title-container {
  display: flex;
  justify-content: center;
}

.experience-title {
  padding-top: 100px;
}

.past-experience-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.container {
  background-color: rgba(249, 248, 246, 255);

  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.content {
  overflow: auto;
  flex: 1 1;
}

html,
body {
  margin: 0;
  background-color: rgba(249, 248, 246, 255);
  height: 100%;
}

